import { useFormik } from "formik";
import * as Yup from "yup";
import { motion } from "framer-motion";
import { Product } from "../types/Product";
import { useDispatch } from "react-redux";
import { AppDispatch } from "../store/store";
import { addProduct, updateProduct } from "../store/productSlice";
import { Modal, Box, TextField, Button, Typography } from "@mui/material";

const style = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "white",
  borderRadius: 2,
  boxShadow: 24,
  p: 4,
};

const validationSchema = Yup.object({
  name: Yup.string().required("Name is require"),
  price: Yup.number().required("Price is require"),
  imageUrl: Yup.string().required("Image URL is require"),
});

export default function ProductModal({
  open,
  close,
  mode,
  product,
}: {
  open: boolean;
  close: () => void;
  mode: "add" | "edit";
  product?: Product;
}) {
  const dispatch = useDispatch<AppDispatch>();

  const formik = useFormik<Product>({
    initialValues: product || { name: "", price: 0, imageUrl: "" },
    validationSchema,
    onSubmit: async (values) => {
      mode === "add"
        ? await dispatch(addProduct(values))
        : await dispatch(updateProduct({ ...values, _id: product!._id }));
      close();
    },
  });

  return (
    <Modal open={open} onClose={close}>
      <motion.div initial={{ scale: 0.6 }} animate={{ scale: 1 }}>
        <Box sx={style}>
          <Typography variant="h6">
            {mode === "add" ? "Insert new product" : "Update product"}
          </Typography>

          <form onSubmit={formik.handleSubmit}>
            <TextField
              fullWidth
              margin="normal"
              label="Nom"
              {...formik.getFieldProps("name")}
              error={!!formik.errors.name}
              helperText={formik.errors.name}
            />
            <TextField
              fullWidth
              type="number"
              margin="normal"
              label="Prix"
              {...formik.getFieldProps("price")}
              error={!!formik.errors.price}
              helperText={formik.errors.price}
            />
            <TextField
              fullWidth
              margin="normal"
              label="Image URL"
              {...formik.getFieldProps("imageUrl")}
              error={!!formik.errors.imageUrl}
              helperText={formik.errors.imageUrl}
            />

            <Button type="submit" variant="contained" sx={{ mt: 2 }} fullWidth>
              {mode === "add" ? "Insert" : "Update"}
            </Button>
          </form>
        </Box>
      </motion.div>
    </Modal>
  );
}
