import axios from "axios";
import { Product } from "../types/Product";

const API_URL = process.env.REACT_APP_API_URL || "http://localhost:5000/api/products";

export const productApi = {
    getAll: () => axios.get(API_URL),
    add: (product: Product) => axios.post(API_URL, product),
    update: (id: string, product: Product) => axios.put(`${API_URL}/${id}`, product),
    delete: (id: string) => axios.delete(`${API_URL}/${id}`)
};